'use client'
import useglobalStore from "@/context/GlobalStore";
import { useRouter } from "next/navigation";
import { useEffect } from "react";


const LocalGame = () =>
{
    const manager = useglobalStore();
    const router = useRouter();
    useEffect(()=>
    {
        if (manager.socket)
        {
            const data = {type: "game", content: {gameType: "local", player: {id: manager.user?.id}}};
            manager.socket.send(JSON.stringify(data));
            manager.socket.onmessage = (msg) =>
            {
                const packet = JSON.parse(msg.data.toString());
                console.log("local: ", packet);
            }
        }
    }, [manager.socket])

    return (
    <>
            <div className="h-screen w-screen flex items-center justify-center gap-x-10 ">
                <div onClick={()=>{
                    const data = {type: "game", content: {gameType: "local", player: {id: "1"}}};
                    manager.socket?.send(JSON.stringify(data));
                }} className="size-40 bg-green-300 text-[20px] flex items-center justify-center">player1</div>
                <div onClick={()=>{
                    const data = {type: "game", content: {gameType: "local", player: {id: "2"}}};
                    manager.socket?.send(JSON.stringify(data));
                }}className="size-40 bg-green-300 text-[20px] flex items-center justify-center">player2</div>
            </div>
        </>
);
}

export default LocalGame;